.container {
  width: 100%;
  min-width: 320px;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 22px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 40px;
  }
}
@media (min-width: 1200px) {
  .container {
    padding: 0 80px;
  }
}

@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-light.woff2") format("woff2"), url("../fonts/montserrat-light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-black.woff2") format("woff2"), url("../fonts/montserrat-black.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: normal;
  font-family: "Montserrat", "Helvetica", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

main {
  flex: 1 1 auto;
}

body {
  cursor: url("../img/cursor.png"), auto;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock {
  overflow: hidden;
}

@keyframes scale {
  0% {
    transform: scale(1);
  }
  70% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1.47);
  }
}
@keyframes bottom-wrapper {
  0% {
    bottom: -1000px;
  }
  70% {
    bottom: 0;
  }
  100% {
    bottom: -3px;
  }
}
@keyframes bottom-logo {
  0% {
    bottom: 0;
    opacity: 0;
  }
  50% {
    bottom: 10%;
    opacity: 1;
  }
  100% {
    bottom: 8%;
    opacity: 1;
  }
}
.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  min-width: 122px;
  font-weight: 700;
  font-size: 10px;
  color: #e9e9e9;
  letter-spacing: 2px;
  text-transform: uppercase;
  background-color: #ff812e;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 300ms;
  user-select: none;
}
.btn svg {
  color: #eae7e5;
  margin-right: 7px;
  transition: all 300ms;
}
@media (min-width: 768px) {
  .btn {
    font-size: 11px;
    letter-spacing: 2.4px;
  }
  .btn svg {
    width: 14px;
    height: 14px;
  }
}
.btn:hover, .btn:focus, .btn:active {
  color: #ffffff;
  background-color: #ea5d00;
  box-shadow: 0 0 13px 0 #ff812e;
}
.btn:hover svg, .btn:focus svg, .btn:active svg {
  color: #ffffff;
}
.btn:focus-visible {
  outline: none;
}
.btn:disabled {
  background-color: #fdc6a2;
  pointer-events: none;
}
.btn:disabled span {
  display: none;
}
.btn:disabled svg {
  transform: rotate(90deg);
  margin: 0;
  color: #ffffff;
}

.new-items__text {
  display: inline-grid;
  grid-template-columns: repeat(3, auto);
  align-items: start;
  gap: 16px;
  margin-bottom: 30px;
}
.new-items__text h2 {
  font-size: 20px;
  font-weight: 900;
  margin: 0;
}
.new-items__text svg {
  margin-top: 4px;
}
@media (min-width: 768px) {
  .new-items__text {
    margin-bottom: 33px;
    gap: 15px;
  }
  .new-items__text h2 {
    font-size: 19px;
  }
}

.new-items__link {
  color: #ff812e;
  margin-top: 4px;
}
@media (min-width: 768px) {
  .new-items__link {
    font-size: 11px;
  }
}

.new-items__container {
  position: relative;
}

.new-items__wrapper {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.new-items__slide {
  display: flex;
  flex-direction: column;
  padding: 10px;
  transition: all 800ms;
  max-width: 160px;
  cursor: default;
  height: auto;
}
@media (min-width: 768px) {
  .new-items__slide {
    max-width: none;
    width: 23.2%;
    padding: 17px 19px 39px 20px;
  }
  .new-items__slide:hover {
    box-shadow: 0 0 66px 0 #ff812e;
  }
  .new-items__slide:hover img {
    transform: scale(1.09);
  }
}

.new-items__additional {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 2px;
  position: relative;
}
.new-items__additional > span {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  background-color: #ff812e;
  border-radius: 25px;
  padding: 5px 10px;
  font-size: 8px;
  font-weight: 700;
  letter-spacing: 2.5px;
  text-transform: uppercase;
  color: #ffffff;
  line-height: normal;
  position: absolute;
  left: 0;
  top: 0;
}
.new-items__slide--new .new-items__additional > span {
  background-color: #81db45;
}
@media (min-width: 768px) {
  .new-items__additional {
    justify-content: space-between;
    align-items: center;
  }
  .new-items__additional > span {
    display: none;
  }
  .new-items__slide--new .new-items__additional > span {
    display: flex;
    min-width: 120px;
    top: -1px;
  }
}

.new-items__delivery {
  display: none;
  grid-template-columns: 24px auto;
  font-size: 8px;
  font-weight: 700;
  letter-spacing: 2.56px;
  text-transform: uppercase;
  gap: 6px;
}
@media (min-width: 768px) {
  .new-items__delivery {
    display: grid;
  }
}

.new-items__info {
  padding: 0 7px 10px 7px;
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 18px;
}
.new-items__info h3 {
  font-size: 12px;
  margin: 0;
  font-weight: 300;
  line-height: normal;
}
.new-items__info p {
  display: none;
  font-size: 12px;
  margin: 0;
  font-weight: 300;
  line-height: normal;
}
.new-items__info > span {
  text-align: center;
  font-size: 18px;
  font-weight: 900;
  margin-top: auto;
}
@media (min-width: 768px) {
  .new-items__info {
    gap: 25px;
  }
  .new-items__info p {
    display: block;
  }
}

.new-items__like {
  cursor: pointer;
}
.new-items__like svg {
  stroke: #000000;
  color: rgba(255, 255, 255, 0);
  transition: all 800ms ease;
}
.new-items__like:hover svg {
  stroke: #ff812e;
  color: #ff812e;
  filter: drop-shadow(0px 0px 13px rgba(255, 129, 46, 0.56));
}
.new-items__like:active svg {
  stroke: #000000;
  color: #ff812e;
  transform: scale(0.5);
}
.new-items__like.is-active svg {
  stroke: #000000;
  color: #ff812e;
}

.new-items__image-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.new-items__image-wrapper img {
  object-fit: cover;
  width: 100%;
  height: auto;
  transition: all 800ms ease;
}
@media (min-width: 768px) {
  .new-items__image-wrapper img {
    width: 163px;
    height: 163px;
  }
}

.new-items__button {
  font-size: 8px;
  cursor: pointer;
  align-self: center;
  align-items: center;
  padding: 15px 10px 10px 10px;
}
@media (min-width: 375px) {
  .new-items__button {
    font-size: 10px;
    min-width: 144px;
  }
}

.new-items__swiper-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 13.68px;
  color: #828282;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  padding: 10px;
  border: none;
  background-color: #e9e9e9;
  transition: all 300ms;
  cursor: pointer;
}
.new-items__swiper-button svg {
  transition: all 300ms;
}
@media (min-width: 768px) {
  .new-items__swiper-button {
    width: 50px;
    height: 47px;
    border-radius: 19px;
  }
  .new-items__swiper-button svg {
    width: 20px;
    height: 23px;
  }
}
.new-items__swiper-button:hover, .new-items__swiper-button:focus, .new-items__swiper-button:active {
  color: #ffffff;
  background-color: #ff812e;
}
.new-items__swiper-button:hover svg, .new-items__swiper-button:focus svg, .new-items__swiper-button:active svg {
  color: #ffffff;
}
.new-items__swiper-button:focus-visible {
  outline: none;
}
.new-items__swiper-button:disabled {
  display: none;
}

.new-items__swiper-button--prev {
  left: 0;
}
.new-items__swiper-button--prev svg {
  transform: rotate(180deg);
}

.new-items__swiper-button--next {
  right: 0;
}

.new-items__description-wrapper {
  padding-left: 14px;
}

.goods {
  padding: 80px 0;
}
.goods h2 {
  font-size: 20px;
  font-weight: 900;
  margin: 0 0 32px 0;
}
@media (min-width: 768px) {
  .goods {
    padding: 72px 0 77px 0;
  }
  .goods h2 {
    font-size: 19px;
    margin-bottom: 43px;
  }
}

.goods__list {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  margin: 0;
  padding: 0;
  gap: 30px 15px;
}
@media (min-width: 768px) {
  .goods__list {
    gap: 30px 3.7%;
  }
}

.goods__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2px 10px;
  color: #000000;
  min-width: 100px;
}
.goods__link svg {
  margin-bottom: 12px;
  transition: all 300ms ease-in;
}
.goods__link h3 {
  font-size: 11px;
  font-weight: 400;
  line-height: normal;
  margin: 0;
  transition: all 300ms ease-in;
}
.goods__link:hover svg, .goods__link:focus svg, .goods__link:active svg {
  color: #ff812e;
}
.goods__link:hover h3, .goods__link:focus h3, .goods__link:active h3 {
  color: #ff812e;
}
.goods__link:focus-visible {
  outline: none;
}
.goods__link:disabled {
  background-color: #fdc6a2;
  pointer-events: none;
}
.goods__link:disabled span {
  display: none;
}
.goods__link:disabled svg {
  transform: rotate(90deg);
  margin: 0;
  color: #ffffff;
}

.goods__link--cutting:hover svg, .goods__link--cutting:focus svg,
.goods__link--cleaning:hover svg,
.goods__link--cleaning:focus svg,
.goods__link--grinding:hover svg,
.goods__link--grinding:focus svg,
.goods__link--brushing:hover svg,
.goods__link--brushing:focus svg,
.goods__link--filing:hover svg,
.goods__link--filing:focus svg,
.goods__link--drilling:hover svg,
.goods__link--drilling:focus svg,
.goods__link--polishing:hover svg,
.goods__link--polishing:focus svg {
  transform: rotate(90deg);
}

.brands__text {
  display: inline-grid;
  grid-template-columns: repeat(3, auto);
  align-items: start;
  gap: 16px;
  margin-bottom: 30px;
}
.brands__text h2 {
  font-size: 20px;
  font-weight: 900;
  margin: 0;
}
.brands__text svg {
  margin-top: 4px;
}
@media (min-width: 768px) {
  .brands__text {
    margin-bottom: 36px;
  }
  .brands__text h2 {
    font-size: 19px;
  }
}

.brands__content-wrapper {
  padding: 0 0 100px 0;
}

.brands__container {
  position: relative;
}

.brands__link {
  color: #ff812e;
  margin-top: 4px;
}
@media (min-width: 768px) {
  .brands__link {
    font-size: 11px;
  }
}

.brands__images-list {
  display: none;
}
@media (min-width: 768px) {
  .brands__images-list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.58%;
    margin-bottom: 60px;
  }
}

.brands__images-item {
  transition: ease-out;
  overflow: hidden;
  position: relative;
}
.brands__images-item::before {
  content: "";
  position: absolute;
  bottom: -500px;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../img/svg/brands-wrapper.svg");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 100%;
  transition: all ease-out;
  animation-duration: 2000ms;
  animation-delay: -1000ms;
}
.brands__images-item::after {
  content: "";
  position: absolute;
  bottom: -1000px;
  transform: translateX(-50%);
  left: 50%;
  width: 113px;
  height: 36px;
  opacity: 0;
  background-image: url("../img/svg/hanko.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
  transition: all ease-out;
  animation-duration: 1000ms;
}
.brands__images-item img {
  animation-duration: 1000ms;
}
.brands__images-item:hover::before {
  animation-name: bottom-wrapper;
  animation-fill-mode: forwards;
  z-index: 1;
}
.brands__images-item:hover::after {
  animation-name: bottom-logo;
  animation-fill-mode: forwards;
  z-index: 10;
}
.brands__images-item:hover img {
  animation-name: scale;
  animation-fill-mode: forwards;
}

.brands__wrapper {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.brands__slide {
  width: fit-content;
  display: flex;
  align-items: center;
  height: auto;
}

.brands__swiper-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 13.68px;
  color: #828282;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  padding: 10px;
  border: none;
  background-color: #e9e9e9;
  transition: all 300ms;
  cursor: pointer;
}
.brands__swiper-button svg {
  transition: all 300ms;
}
@media (min-width: 768px) {
  .brands__swiper-button {
    width: 50px;
    height: 47px;
    border-radius: 19px;
  }
}
.brands__swiper-button:hover, .brands__swiper-button:focus, .brands__swiper-button:active {
  color: #ffffff;
  background-color: #ff812e;
}
.brands__swiper-button:hover svg, .brands__swiper-button:focus svg, .brands__swiper-button:active svg {
