.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  transition: all 300ms;
}
.modal.is-open {
  opacity: 1;
  visibility: visible;
}
.modal.is-open .modal__wrapper {
  transform: translateY(0);
}

.modal__wrapper {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 10px;
  transition: all 300ms ease-out;
  transform: translateY(100vh);
}
@media (min-width: 768px) {
  .modal__wrapper {
    transform: translateY(-100vh);
  }
}

.modal__content {
  background-color: #ffffff;
  color: #000000;
  max-width: 435px;
  padding: 48px 46px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.modal__content h2 {
  font-size: 20px;
  font-weight: 900;
  margin: 0 0 32px 0;
  text-align: center;
}

.modal__close-button {
  display: flex;
  justify-content: center;
  width: 30px;
  height: 30px;
  user-select: none;
  cursor: pointer;
  position: absolute;
  right: 22px;
  top: 22px;
  padding: 3px;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
  /*&:active .modal__left-right {
  	transform: rotate(-45deg);
  }

  &:active .modal__right-left {
  	transform: rotate(45deg);
  }*/
}
.modal__close-button .modal__left-right {
  height: 3px;
  width: 18px;
  top: 15px;
  left: 7px;
  position: absolute;
  background-color: #000000;
  border-radius: 2px;
  transform: rotate(45deg);
  transition: all 300ms;
}
.modal__close-button .modal__right-left {
  height: 3px;
  width: 18px;
  top: 15px;
  right: 5px;
  position: absolute;
  background-color: #000000;
  border-radius: 2px;
  transform: rotate(-45deg);
  transition: all 300ms;
}

.modal__fields-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
  align-self: stretch;
}
.modal__fields-wrapper input,
.modal__fields-wrapper textarea {
  border-radius: 12px;
  display: flex;
  padding: 14px;
  gap: 10px;
  align-self: stretch;
  border: none;
  background-color: #f5f5f5;
}
.modal__fields-wrapper input::placeholder,
.modal__fields-wrapper textarea::placeholder {
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}
.modal__fields-wrapper input {
  align-items: center;
}
.modal__fields-wrapper textarea {
  height: 167px;
  align-items: flex-start;
  flex-shrink: 0;
  resize: none;
  margin-bottom: 16px;
}

.modal__fields-wrapper {
  margin-bottom: 32px;
}

