.modal__file-input input[type=file]:disabled + .modal__file-button {
  background-color: #e0e0e0;
}

.modal__file-button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 12px;
  background-color: #e9e9e9;
  height: 47px;
  width: 58px;
  padding: 10px;
  transition: all 300ms ease-in;
}

.modal__buttons-wrapper {
  display: grid;
  grid-template-columns: repeat(2, min-content);
  gap: 8px;
}

.modal__button {
  padding: 15px 20px;
}
.modal__button svg {
  width: 22px;
  height: 19px;
  margin-right: 22px;
}

.contacts-sidebar {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  transition: all 300ms ease;
}
.contacts-sidebar.is-open {
  opacity: 1;
  visibility: visible;
}
.contacts-sidebar.is-open .contacts-sidebar__wrapper {
  transform: translateX(0);
}
@media (min-width: 768px) {
  .contacts-sidebar {
    background-color: rgba(0, 0, 0, 0.2);
  }
}

.contacts-sidebar__wrapper {
  min-height: calc(100vh - 70px);
  display: flex;
  align-items: start;
  transition: all 300ms ease-out;
  transform: translateY(100vh);
}
@media (min-width: 768px) {
  .contacts-sidebar__wrapper {
    transform: translateX(-100vw);
  }
}

.contacts-sidebar__content {
  padding: 17px 16px 53px 16px;
  width: 100%;
  background-color: #ffffff;
  align-self: stretch;
  position: relative;
  top: 70px;
}
@media (min-width: 768px) {
  .contacts-sidebar__content {
    top: 0;
    max-width: 518px;
  }
}

.contacts-sidebar__header {
  display: grid;
  grid-template-columns: 15px 1fr 14px;
  gap: 16px;
  align-items: center;
  padding: 0 0 17px 54px;
}
.contacts-sidebar__header span {
  font-size: 20px;
  font-weight: 900;
}

.contacts-sidebar__close-element {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 14px;
  height: 22px;
  user-select: none;
  cursor: pointer;
  padding: 3px;
  border: none;
  background-color: #ffffff;
}
.contacts-sidebar__close-element svg {
  display: none;
}
@media (min-width: 768px) {
  .contacts-sidebar__close-element svg {
    display: block;
    flex-shrink: 0;
  }
}

.contacts-sidebar__main {
  padding: 32px 61px 0 64px;
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.contacts-sidebar__main::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #000000;
}

.contacts-sidebar__subway-wrapper {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
}

.contacts-sidebar__subway {
  display: grid;
  grid-template-columns: 20px auto;
  gap: 8px;
  align-items: center;
}
.contacts-sidebar__subway span {
  font-size: 16px;
}

.contacts-sidebar__address {
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
}

.contacts-sidebar__working-hours {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 12px;
  font-size: 14px;
}

.contacts-sidebar__contacts-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  gap: 4px;
  font-weight: 400;
  font-size: 14px;
  margin-bottom: 16px;
}

.contacts-sidebar__tel {
  color: #000000;
}

.contacts-sidebar__email {
  color: #ff812e;
}

.contacts-sidebar__social {
  grid-template-columns: repeat(4, 20px);
  margin: 0;
}
@media (min-width: 768px) {
  .contacts-sidebar__social {
    grid-template-columns: repeat(3, 20px);
  }
}

@media (min-width: 768px) {
  .social__item--whatsapp {
    display: none;
  }
}

.contacts-sidebar__details {
  margin-bottom: 174px;
}
.contacts-sidebar__details span {
  display: block;
}
.contacts-sidebar__details span:not(:first-child) {
  font-weight: 300;
}

.contacts-sidebar__button {
  margin-top: auto;
}
.contacts-sidebar__button svg {
  width: 20px;
  height: 19px;
  margin-right: 22px;
}

.search {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  transition: all 300ms;
}
.search.is-open {
  opacity: 1;
  visibility: visible;
}
.search.is-open .search__wrapper {
  transform: translateX(0);
}
@media (min-width: 768px) {
  .search {
    background-color: rgba(0, 0, 0, 0.8);
  }
}

.search__wrapper {
  min-height: 100%;
  display: flex;
  align-items: start;
  justify-content: center;
  padding: 250px 50px 10px 50px;
  transition: all 300ms ease-out;
  transform: translateY(-100vh);
}

.search__form {
  display: grid;
  grid-template-columns: 28px 1fr 12px;
  gap: 36px;
  max-width: 940px;
  padding: 17px;
  align-items: center;
  border-radius: 12px;
  background-color: #f5f5f5;
  width: 100%;
}
.search__form input {
  display: flex;
  align-items: center;
  padding: 4px;
  align-self: stretch;
  background-color: #f5f5f5;
  border: none;
  font-family: "Montserrat", "Helvetica", sans-serif;
}
.search__form input::placeholder {
  color: #000000;
}

.search__button,
.search__close-search {
  padding: 0;
  background-color: #f5f5f5;
  border: none;
}

.catalog-sidebar {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0);
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  transition: all 500ms;
}
.catalog-sidebar.is-open {
  opacity: 1;
  visibility: visible;
}
.catalog-sidebar.is-open .catalog-sidebar__wrapper {
  transform: translateX(0);
}
@media (min-width: 768px) {
  .catalog-sidebar {
    background-color: rgba(0, 0, 0, 0.2);
  }
}

.catalog-sidebar__wrapper {
  min-height: 100%;
  display: flex;
  align-items: start;
  transition: all 300ms ease-out;
  transform: translateX(100vw);
}
@media (min-width: 768px) {
  .catalog-sidebar__wrapper {
    transform: translateX(-100vw);
  }
}

.catalog-sidebar__content {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  max-width: 100%;
  position: absolute;
  top: 134px;
  padding: 0 15px 21px 15px;
}
@media (min-width: 768px) {
  .catalog-sidebar__content {
    max-width: 340px;
    top: 0;
    padding: 21px 15px;
  }
}

.catalog-sidebar__header {
  padding: 0 0 20px 82px;
  display: none;
}
@media (min-width: 768px) {
  .catalog-sidebar__header {
    display: block;
  }
}

.catalog-sidebar__close-button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  width: 100%;
  padding: 0;
  gap: 20px;
  font-size: 20px;
  font-weight: 900;
}
.catalog-sidebar__close-button svg {
  flex-shrink: 0;
}

.catalog-sidebar__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  overflow-y: scroll;
}
@media (min-width: 768px) {
  .catalog-sidebar__list {
    position: relative;
  }
  .catalog-sidebar__list::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #000000;
  }
}

.catalog-sidebar__link {
  display: grid;
  grid-template-columns: 38px 1fr;
  align-items: center;
  gap: 34px;
  padding: 14px 0 16px 10px;
  color: #000000;
}
.catalog-sidebar__link svg {
  transition: all 300ms ease-in;
  align-self: start;
}
.catalog-sidebar__link h3 {
  font-size: 14px;
  font-weight: 300;
  margin: 0;
  transition: all 300ms ease-in;
}
.catalog-sidebar__link:hover svg, .catalog-sidebar__link:focus svg, .catalog-sidebar__link:active svg {
  color: #ff812e;
}
.catalog-sidebar__link:hover h3, .catalog-sidebar__link:focus h3, .catalog-sidebar__link:active h3 {
  color: #ff812e;
}
.catalog-sidebar__link:focus-visible {
  outline: none;
}
.catalog-sidebar__link:disabled {
  background-color: #fdc6a2;
  pointer-events: none;
}

.catalog-sidebar__link--cutting,
.catalog-sidebar__link--cleaning,
.catalog-sidebar__link--grinding,
.catalog-sidebar__link--brushing,
.catalog-sidebar__link--scroll-saw,
.catalog-sidebar__link--filing,
.catalog-sidebar__link--drilling,
.catalog-sidebar__link--polishing,
.catalog-sidebar__link--tool {
  position: relative;
}
.catalog-sidebar__link--cutting::before,
.catalog-sidebar__link--cleaning::before,
.catalog-sidebar__link--grinding::before,
.catalog-sidebar__link--brushing::before,
.catalog-sidebar__link--scroll-saw::before,
.catalog-sidebar__link--filing::before,
.catalog-sidebar__link--drilling::before,
.catalog-sidebar__link--polishing::before,
.catalog-sidebar__link--tool::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #f4f4f4;
}